.dateTimePill {
  background: var(--color-gray-500) !important;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 0px 4px 0px 4px;
}
.dateTimePill.selected {
  outline: 2px solid rgb(60, 132, 244);
}
.dateTimePill:hover {
  background: #f2f2f2;
}
.dateTimePicker {
  background: #fff;
  border: 1px solid #ddd;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  padding: 0px 6px 0px 10px;
}
